<template>
  <div>
    <v-container class="especialidad__container">
      <title-ribbon v-bind="{ title: especialidad.NombreCompleto }" />
      <div
        v-if="charged"
        class="especialidad_container__main"
      >
        <v-row class="especialidad_row__img">
          <v-col>
            <v-img
              :src="
                `https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto,q_auto,c_scale/v1637797361/terapiacl/especialidades/${especialidad.Imagen}`
              "
            />
          </v-col>
        </v-row>
        <splitter />
        <v-row class="especialidad_row__descripcion1 mb-4">
          <v-col>
            <v-row>
              <v-col class="col-12 col-md-9 descripcion">
                <p v-html="especialidad.DescripcionGlosaFirst.Descripcion" />
                <v-row v-if="especialidad.Problemas.length > 0">
                  <div class="erd__divider" />
                  <v-col
                    v-for="(item, i) in especialidad.Problemas"
                    :key="i"
                    class="col-12 col-lg-6 box"
                  >
                    <div class="principal">
                      <div class="up">
                        <h3>{{ item[0].NombreTipoProblema }}</h3>
                      </div>
                      <ul>
                        <li
                          v-for="(problema, p) in item"
                          :key="p"
                        >
                          <router-link :to="`/problema/${problema.ToLink}`">
                            {{ problema.NombreProblema }}
                          </router-link>
                        </li>
                      </ul>
                    </div>
                  </v-col>
                </v-row>
              </v-col>
              <v-col class="col-12 col-md-3 sideview">
                <div class="sideview-main">
                  <!-- <h3>Reserva tu Hora</h3> -->
                  <v-btn
                    color="success"
                    href="http://www.centrodeterapia.cl/horas_online/"
                    target="_blank"
                  >
                    Reserva tu Hora
                  </v-btn>
                  <div
                    v-if="
                      especialidad.CodSiat !== 9 && especialidad.CodSiat !== 91
                    "
                  >
                    <p
                      v-for="(sucursal, i) in sucursales"
                      :key="i"
                    >
                      <router-link
                        :to="
                          `/terapeutas/${sucursal.ToLink}/${especialidad.ToLink}`
                        "
                      >
                        {{ especialidad.NombreEspecialistas }}
                        {{ sucursal.Nombre }}
                      </router-link>
                    </p>
                  </div>
                  <p>
                    <router-link
                      to="/aranceles-psicologia"
                      aria-label="Aranceles Psicología"
                    >
                      Nuestros Aranceles
                    </router-link>
                  </p>
                  <div>
                    <p>Otras especialidades:</p>
                    <ul>
                      <li
                        v-for="(item, i) in especialidadesMenu"
                        :key="i"
                      >
                        <router-link :to="`/especialidades/${item.ToLink}`">
                          {{ item.NombreCompleto }}
                        </router-link>
                      </li>
                    </ul>
                  </div>
                </div>
              </v-col>
            </v-row>
          </v-col>
        </v-row>
        <splitter v-if="especialidad.DescripcionGlosaRest.length > 0" />
        <v-row
          v-if="especialidad.DescripcionGlosaRest.length > 0"
          class="especialidad_row__descripcion2"
        >
          <v-col>
            <p
              v-for="(glosa, i) in especialidad.DescripcionGlosaRest"
              :key="i"
              v-html="glosa.Descripcion"
            />
          </v-col>
        </v-row>
        <splitter />
        <message-ribbon
          v-bind="{
            msg2: `Si desea atenderse con uno de nuestros ${especialidad.NombreEspecialistas.toLowerCase()}, reserve su hora y lo esperamos
              en nuestros centros ubicados en: <b> Providencia, Santiago Centro y Viña del Mar o Terapia Online</b>`,
          }"
        />
        <splitter-volver />
      </div>
      <div v-else>
        <v-row>
          <v-col class="text-center">
            <v-progress-circular
              :size="70"
              :width="7"
              color="success"
              indeterminate
            />
          </v-col>
        </v-row>
      </div>
    </v-container>
  </div>
</template>

<script>
  import Vue from 'vue'
  import { sortByAlbhabet } from '@/helpers'
  import { mapState } from 'vuex'
  export default {
    name: 'Especialidad',
    components: {
      TitleRibbon: () => import('@/components/web/base/TitleRibbon'),
      MessageRibbon: () => import('@/components/web/base/MessageRibbon'),
      Splitter: () => import('@/components/web/base/Splitter'),
      SplitterVolver: () => import('@/components/web/base/SplitterVolver'),
    },
    data: function () {
      return {
        especialidad: '',
        especialidadesMenu: '',
        charged: false,
      }
    },
    computed: {
      ...mapState('web', ['sucursales']),
    // especialidadesMenuSorted () {
    //   const arr = this.especialidadesMenu
    //   console.log(arr)
    //   const arrSort = arr.sort((a, b) => a.Nombre.normalize().localeCompare(b.Nombre.normalize()))
    //   return arrSort
    // },
    },
    watch: {
      '$route.params.toLink': {
        handler: function (toLink) {
          this.getEspecialidad(toLink)
        },
      },
    },
    created () {
      this.getEspecialidad(this.$route.params.toLink)
      this.getEspecialidadesCombos()
    },
    methods: {
      getEspecialidad (toLink) {
        this.charged = false
        this.especialidad = ''
        const body = {
          addMeta: true,
          addProblemas: true,
          addDescripcionGlosa: true,
          addNombreEspecialistasPlural: true,
        }
        Vue.axios
          .post(`/api/especialidades/${toLink}`, body)
          .then(response => {
            this.especialidad = response.data.result[0]
            // console.log(this.especialidad)
            this.charged = true
          })
          .catch(error => {
            console.log(error)
          })
      },
      getEspecialidadesCombos () {
        this.especialidadesMenu = ''
        Vue.axios
          .get('/api/especialidades/combos?toLink=sucursal-providencia')
          .then(response => {
            this.especialidadesMenu = sortByAlbhabet(
              response.data.result,
              'NombreCompleto',
            )
          })
          .catch(error => {
            console.log(error)
          })
      },
    },
    metaInfo () {
      return {
        title: this.especialidad.MetaTitle
          ? this.especialidad.MetaTitle
          : ' ... ',
        titleTemplate: '%s | Centro de Terapia del Comportamiento',
        htmlAttrs: {
          lang: 'es',
          amp: true,
        },
        meta: this.especialidad.MetaTags ? this.especialidad.MetaTags : [],
      }
    },
  }
</script>

<style>
.especialidad_container {
  padding: 50px !important;
  text-align: center;
}
</style>
